.container {
    background-color: #f5fffa;
    color: white;
    padding: 3rem 2rem;
    font-family: Arial, sans-serif;
  }
  
  .title {
    font-size: 2.5rem;
    font-weight: bold;
    margin-bottom: 1rem;
    text-align: center;
    color: #2C2C2C;
    font-family: 'Merriweather', serif;
  }
  
  .subtitle {
    font-size: 1.1rem;
    margin-bottom: 3rem;
    max-width: 800px;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    line-height: 1.6;
    color: black;
    font-family: 'Source Sans Pro', sans-serif;
    font-size: 1.3rem;
  }
  
  .grid {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 2rem;
    max-width: 1200px;
    margin: 0 auto;
  }
  
  @media (min-width: 768px) {
    .grid {
      grid-template-columns: repeat(3, 1fr);
    }
  }
  
  .card {
    background-color: white;
    border-radius: 1rem;
    overflow: hidden;
    box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
  }
  
  .card:hover {
    transform: translateY(-5px);
    box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
  }
  
  .imageContainer {
    position: relative;
    height: 200px;
  }
  
  .image {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
  .iconOverlay {
    position: absolute;
    top: 1rem;
    left: 1rem;
    background-color: #FF6600;
    color: white;
    border-radius: 50%;
    width: 3.5rem;
    height: 3.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.5rem;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  }
  
  .cardContent {
    padding: 1.5rem;
  }
  
  .cardTitle {
    font-size: 1.25rem;
    font-weight: 600;
    margin-bottom: 0.75rem;
    color: #2C2C2C;
    font-family: 'Merriweather', serif;
  }
  
  .cardDescription {
    margin-bottom: 1.5rem;
    line-height: 1.5;
    font-family: 'Source Sans Pro', sans-serif;
    color:#1B365D;
    font-weight: 500;
  }
  .learnMoreButton {
    display: inline-flex;
    align-items: center;
    background-color: #FF6600;
    color:  #fcf8d6;
    padding: 0.5rem 1rem;
    border-radius: 9999px;
    text-decoration: none;
    font-weight: 600;
    transition: background-color 0.3s ease;
    font-family: 'Raleway', sans-serif;
  }
  
  .learnMoreButton:hover {
    background-color: #002366;
  }
  
  .learnMoreButton span {
    margin-right: 0.5rem;
  }
  
  .arrowCircle {
    background-color: #fff5ee;
    border-radius: 50%;
    width: 24px;
    height: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 0.5rem;
  }
  
  .arrowIcon {
    width: 16px;
    height: 16px;
    color: #002366;
    transform: rotate(-45deg);
  }
  .footer {
    margin-top: 3rem;
    text-align: center;
    font-size: 1.1rem;
    max-width: 800px;
    margin-left: auto;
    margin-right: auto;
  }